import { Modal } from 'antd';
import { TFunction } from 'i18next';

const { confirm } = Modal;

const showDeleteConfirm = (email: string, onConfirm: () => void, translation: TFunction): void => {
  confirm({
    title: translation('permissionSelectMenu.removeMember.title'),
    content: email,
    okText: translation('base.cancel'),
    cancelText: translation('permissionSelectMenu.removeMember.remove'),
    okButtonProps: { type: 'primary' },
    cancelButtonProps: { type: 'default' },
    className: 'deleteMemberConfirm',
    style: {
      height: '152px',
      paddingBottom: 0,
    },
    onCancel: onConfirm,
    keyboard: false,
    maskClosable: false,
  });
};

export default showDeleteConfirm;
