import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { AreaProfileName, EditIcon, WorkspaceNameHeader, WorkspaceNameText } from './styles';

interface IWorkspaceName {
  canChangeName: boolean;
  workspaceName: string;
  onNameChange: (newName: string) => void;
}

export const WorkspaceName: React.FC<IWorkspaceName> = props => {
  const { canChangeName, workspaceName, onNameChange } = props;

  const [isEditing, setIsEditing] = useState<boolean>(false);
  const [currentName, setCurrentName] = useState<string>(workspaceName);
  const [isHovered, setIsHovered] = useState<boolean>(false);

  const { t: translation } = useTranslation();

  useEffect(() => {
    if (!isEditing) {
      setCurrentName(workspaceName);
    }
  }, [workspaceName, isEditing]);

  const onFinishEditing = (): void => {
    const newName = currentName.trim().replace(/\s+/ig, ' ');

    if (newName) {
      onNameChange(newName);
    } else {
      setCurrentName(workspaceName);
    }

    setIsEditing(false);
    setIsHovered(false);
  };

  const onEditingStartClick = (): void => {
    if (canChangeName) {
      setIsEditing(true);
    }
  };

  if (!isEditing) {
    return (
      <WorkspaceNameHeader
        onClick={onEditingStartClick}
        onMouseEnter={(): void => setIsHovered(true)}
        onMouseLeave={(): void => setIsHovered(false)}
        hasCursorPointer={canChangeName}
      >
        <WorkspaceNameText>
          {currentName}
        </WorkspaceNameText>
        {canChangeName ? <EditIcon active={isHovered} /> : null}
      </WorkspaceNameHeader>
    );
  }

  return (
    <AreaProfileName
      id='workspaceNameInput'
      autoFocus={true}
      onKeyDown={(event): void => {
        if (event.key === 'Enter') {
          event.preventDefault();
          onFinishEditing();
        }
      }}
      onChange={(event): void => setCurrentName(event.currentTarget.value)}
      onBlur={(): void => {
        onFinishEditing();
      }}
      defaultValue={currentName}
      value={currentName}
      placeholder={translation('personalArea.workspaceSettings.workspaceNamePlaceholder') || ''}
    />
  );
};
