import styled from '@emotion/styled';
import { Input } from 'antd';

import { ModernModal } from '../../../../ui/modern-modal';

export const DescriptionText = styled('p')`
  margin: 0;
`;

export const DescriptionContainer = styled('div')`
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0;
  color: var(--767676-members-page);
  
  strong {
    font-weight: 500;
  }
`;

export const SubDescriptionContainer = styled('div')`
  row-gap: 4px;
  flex-direction: column;
  display: flex;
  margin: 12px 0 16px;
`;

export const ActionWorkspaceModal = styled(ModernModal)`
  .ant-modal-title {
    width: 300px;

    b, strong {
      font-weight: 500;
    }
  }
`;

export const TitleContainer = styled('div')`
  color: var(--222222-members-page);
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
`;

export const InputContainer = styled(Input)`
  margin-top: 8px;
  letter-spacing: 0;
  padding: 8px;
`;
