import { Tooltip } from 'antd';
import React from 'react';

import IconPerson from '../../../../ui/icons/IconPerson';
import { EmailCell, EmailColumn, EmailTooltip } from '../../../../ui/styles';

interface ICellEmail {
  email: string;
}

const CellEmail: React.FC<ICellEmail> = ({ email }) => (
  <EmailCell>
    <IconPerson />
    <Tooltip
      title={(
        <EmailTooltip>
          {email}
        </EmailTooltip>
      )}
    >
      <EmailColumn>
        {email}
      </EmailColumn>
    </Tooltip>
  </EmailCell>
);

export default CellEmail;
