import React, { useCallback, useContext, useEffect, useState } from 'react';
import { Trans, useTranslation } from 'react-i18next';

import { DescriptionContainer, DescriptionText, InputContainer, SubDescriptionContainer, TitleContainer } from './styles';
import { FREE_PLAN_ID } from '../../../../../common/constants/constants';
import { workspaceContext } from '../../../../state';
import ActionButtons from '../../../../ui/action-buttons';
import MaterialModal from '../../../../ui/material-modal';

interface IDeleteWorkspaceModal {
  isVisible: boolean;
  setIsVisible: (isVisible: boolean) => void;
  onDeleteConfirm: () => void;
}

const DeleteWorkspaceModal: React.FC<IDeleteWorkspaceModal> = (props) => {
  const { isVisible, setIsVisible, onDeleteConfirm } = props;

  const [deleteWorkspaceInputValue, setDeleteWorkspaceInputValue] = useState('');

  const { paymentIsTrial = true, isUnpaid, planId, planExpiresAt, profilesCount, name: workspaceName } = useContext(workspaceContext);

  const hideModal = useCallback(() => setIsVisible(false), [setIsVisible]);

  const { t: translation } = useTranslation();

  const isFreePlanWorkspace = isUnpaid || planId === FREE_PLAN_ID;
  const isTrialOrFreePlan = isFreePlanWorkspace || paymentIsTrial;

  useEffect(() => {
    if (!isVisible) {
      setDeleteWorkspaceInputValue('');
    }
  }, [isVisible]);

  const onDeleteClick = (): void => {
    if (deleteWorkspaceInputValue === 'Delete') {
      onDeleteConfirm();
    }
  };

  const renderExpiresPaidDate = (): string => new Date(planExpiresAt)
    .toLocaleDateString('en-EN', { year: 'numeric', month: '2-digit', day: '2-digit' });

  const titleKey = isTrialOrFreePlan ?
    'workspaceSettingsMembers.deleteWorkspaceModal.unpaidWsTitle' :
    'workspaceSettingsMembers.deleteWorkspaceModal.paidWsTitle';

  return (
    <MaterialModal
      isVisible={isVisible}
      onClose={hideModal}
      width='416px'
    >
      <TitleContainer>
        <Trans i18nKey={titleKey} values={{ name: workspaceName }} />
      </TitleContainer>
      <DescriptionContainer>
        <SubDescriptionContainer>
          {!isTrialOrFreePlan ? (
            <div>
              <Trans i18nKey='workspaceSettingsMembers.deleteWorkspaceModal.paymentInfo' values={{ name: renderExpiresPaidDate() }} />
            </div>
          ) : null}
          <div>
            <Trans i18nKey='workspaceSettingsMembers.deleteWorkspaceModal.numberOfProfiles' count={profilesCount} />
          </div>
        </SubDescriptionContainer>
        <DescriptionText style={{ marginTop: 12 }}>
          {translation('deleteWorkspaceModal.secondDescription')}
          {' '}
          <span>
            Delete
          </span>
          {' '}
          {translation('deleteWorkspaceModal.below')}
        </DescriptionText>
      </DescriptionContainer>
      <InputContainer
        placeholder={translation('deleteWorkspaceModal.deleteInputPlaceholder') || ''}
        value={deleteWorkspaceInputValue}
        onChange={(event): void => setDeleteWorkspaceInputValue(event.target?.value)}
      />
      <ActionButtons
        confirmText={translation('deleteWorkspaceModal.confirmDelete')}
        isDisabledConfirm={deleteWorkspaceInputValue !== 'Delete'}
        onConfirm={onDeleteClick}
        onCancel={hideModal}
        isDangerous={true}
      />
    </MaterialModal>
  );
};

export default DeleteWorkspaceModal;
