import React from 'react';

import { ModalContent, CustomModal } from './styles';

interface IMaterialModal {
  isVisible: boolean;
  onClose: () => void;
  width?: string;
}

const MaterialModal: React.FC<IMaterialModal> = (props) => {
  const { isVisible, onClose, children, width = 'auto' } = props;

  return (
    <CustomModal
      open={isVisible}
      onClose={onClose}
    >
      <ModalContent width={width}>
        {children}
      </ModalContent>
    </CustomModal>
  );
};

export default MaterialModal;
