import { PopoverOrigin, TableCell } from '@material-ui/core';
import moment from 'moment';
import React, { FC, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { CustomMenu, SelectRoleMenuItem } from './styles';
import { IMySharedAccount } from '../../../interfaces';
import { CustomTableRow } from '../../../ui/styles';
import { getInviteActionText } from '../../../utils/roles-texts';
import CellEmail from '../member-row/email';
import CellInvited from '../member-row/invited';
import showDeleteConfirm from '../member-row/permissions/delete-member-confirm';
import RoleDescription from '../role-select/role-description';
import RoleTitle from '../role-select/role-title';
import { SelectButton } from '../role-select/select-button';

const INITIAL_TRANSFORM_ORIGIN: PopoverOrigin = { vertical: 0, horizontal: 8 };

interface IMySharedAccountRow {
  mySharedAccount: IMySharedAccount;
  onRemoveMySharedAccount: () => Promise<void>;
}

const MySharedAccountRow: FC<IMySharedAccountRow> = (props) => {
  const { mySharedAccount, onRemoveMySharedAccount } = props;

  const [anchorEl, setAnchorEl] = useState<HTMLElement|null>(null);

  const { t: translation } = useTranslation();

  const { from, to, createdAt } = mySharedAccount;

  const onClose = (): void => {
    setAnchorEl(null);
  };

  const onDeletePressed = (): void => {
    onClose();
    showDeleteConfirm(to.email, () => onRemoveMySharedAccount(), translation);
  };

  return (
    <CustomTableRow>
      <TableCell style={{ paddingLeft: 0, width: '220px' }}>
        <CellEmail email={to.email} />
      </TableCell>
      <TableCell>
        <SelectButton
          roleText={getInviteActionText('mySharedAccount', translation)}
          onClick={(event): void => setAnchorEl(event.currentTarget)}
          width='264px'
          height='20px'
        />
        <CustomMenu
          open={!!anchorEl}
          anchorEl={anchorEl}
          onClose={onClose}
          getContentAnchorEl={null}
          anchorOrigin={{ vertical: 'bottom', horizontal: 'left' }}
          transformOrigin={INITIAL_TRANSFORM_ORIGIN}
        >
          <SelectRoleMenuItem onClick={onDeletePressed}>
            <div>
              <RoleTitle role='remove' />
              <RoleDescription type='invite' role='mySharedAccount' />
            </div>
          </SelectRoleMenuItem>
        </CustomMenu>
      </TableCell>
      <TableCell style={{ width: '160px' }} />
      <TableCell
        style={{ width: '220px', overflow: 'hidden', textOverflow: 'ellipsis' }}
      >
        <CellInvited
          invitedByEmail={from.email}
          inviteCreatedAt={moment(createdAt).toDate()}
        />
      </TableCell>
    </CustomTableRow>
  );
};

export default React.memo(MySharedAccountRow);
