import styled from '@emotion/styled';

export const InvitedContainer = styled('div')`
  margin: 10px 8px;
`;

export const InvitedByEmailWrapper = styled('div')`
  font-family: "Roboto";
  font-size: 13px;
  line-height: 20px;
  color: var(--222222-members-row);
  margin-left: 1px;
`;

export const InviteDateWrapper = styled('div')`
  opacity: 0.5;
  font-family: 'Roboto';
  font-size: 13px;
  line-height: 20px;
  color: var(--22222280-members-row);
  margin-left: 1px;
`;

export const InvitedByColumn = styled('span')`
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  cursor: pointer;
  letter-spacing: normal;
  color: var(--000000DE-members-row);
`;
