import React, { useCallback } from 'react';
import { Trans, useTranslation } from 'react-i18next';

import { DescriptionContainer, DescriptionText, TitleContainer } from './styles';
import ActionButtons from '../../../../ui/action-buttons';
import MaterialModal from '../../../../ui/material-modal';

interface ILeaveWorkspaceModal {
  workspaceName: string;
  isVisible: boolean;
  setIsVisible: (isVisible: boolean) => void;
  onLeave: () => void;
}

const LeaveWorkspaceModal: React.FC<ILeaveWorkspaceModal> = (props) => {
  const { workspaceName, isVisible, setIsVisible, onLeave } = props;

  const hideModal = useCallback(() => setIsVisible(false), [setIsVisible]);

  const { t: translation } = useTranslation();

  return (
    <MaterialModal
      isVisible={isVisible}
      onClose={hideModal}
      width='420px'
    >
      <TitleContainer>
        <Trans i18nKey='leaveWorkspaceModal.title' values={{ workspaceName }} />
      </TitleContainer>
      <DescriptionContainer>
        <DescriptionText style={{ marginTop: 20 }}>
          {translation('leaveWorkspaceModal.description')}
        </DescriptionText>
      </DescriptionContainer>
      <ActionButtons
        confirmText={translation('leaveWorkspaceModal.confirmLeave')}
        onConfirm={onLeave}
        onCancel={hideModal}
        isDangerous={true}
      />
    </MaterialModal>
  );
};

export default LeaveWorkspaceModal;
