import { Tooltip } from 'antd';
import React from 'react';
import { useTranslation } from 'react-i18next';

import dateToText from '../../../../utils/date-to-text';
import {
  InviteDateWrapper,
  InvitedByColumn,
  InvitedByEmailWrapper,
  InvitedContainer,
} from './styles';

interface ICellInvited {
  invitedByEmail?: string;
  inviteCreatedAt?: Date;
}

const CellInvited: React.FC<ICellInvited> = (props) => {
  const { invitedByEmail, inviteCreatedAt } = props;

  const { t: translation } = useTranslation();

  if (!inviteCreatedAt) {
    return null;
  }

  return (
    <Tooltip
      title={(
        <InvitedContainer>
          <InvitedByEmailWrapper>
            {translation('personalArea.workspaceSettings.membersTable.invitedBy')}
            &nbsp;
            {invitedByEmail}
          </InvitedByEmailWrapper>
          <InviteDateWrapper>
            {dateToText(inviteCreatedAt)}
          </InviteDateWrapper>
        </InvitedContainer>
      )}
    >
      <InvitedByColumn>
        {invitedByEmail || ''}
      </InvitedByColumn>
    </Tooltip>
  );
};

export default CellInvited;
