import React, { useContext, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { userContext, workspaceContext } from '../../../../state';
import { RedActionButton } from '../../../../ui/action-buttons/styles';
import { deleteWorkspace } from '../../../common/api';
import checkAndSetDefaultWorkspace from '../../../common/check-and-set-default-workspace';
import switchWorkspace from '../../../common/switch-workspace';
import { kickMember } from '../../api';
import DeleteWorkspaceModal from './delete-workspace-modal';
import LeaveWorkspaceModal from './leave-workspace-modal';
import { logout } from '../../../../utils/logout';

interface IWorkspaceActions {}

const WorkspaceActions: React.FC<IWorkspaceActions> = () => {
  const [isDeleteWorkspaceModalVisible, setIsDeleteWorkspaceModalVisible] = useState<boolean>(false);
  const [isLeaveWorkspaceModalVisible, setIsLeaveWorkspaceModalVisible] = useState<boolean>(false);

  const userCtx = useContext(userContext);
  const workspaceCtx = useContext(workspaceContext);
  const { id: workspaceId,
    userId,
    me,
    owner: workspaceOwner,
    name: workspaceName,
    availableWorkspaces,
    updateAvailableWorkspaces,
  } = workspaceCtx;

  const { t: translation } = useTranslation();

  const leaveWorkspace = async (): Promise<void> => {
    setIsDeleteWorkspaceModalVisible(false);
    setIsLeaveWorkspaceModalVisible(false);

    const newWorkspaceList = availableWorkspaces.filter(workspace => workspace.id !== workspaceId);

    updateAvailableWorkspaces(newWorkspaceList);

    checkAndSetDefaultWorkspace(userCtx);
    await switchWorkspace(newWorkspaceList[0].id, workspaceCtx, userCtx);
  };

  const onLeaveWorkspace = (): Promise<[void, void]> => Promise.all([
    kickMember(workspaceId, me?.id || ''),
    leaveWorkspace(),
  ]);

  const onClickLeaveWorkspace = (): void => setIsLeaveWorkspaceModalVisible(true);

  const onWorkspaceDeleteConfirm = (): void => {
    deleteWorkspace(workspaceId).then(() => leaveWorkspace());
  };

  const onClickDeleteWorkspace = (): void => setIsDeleteWorkspaceModalVisible(true);

  const isWorkspaceOwner = userId === workspaceOwner;

  return (
    <>
      {!isWorkspaceOwner ? (
        <RedActionButton onClick={onClickLeaveWorkspace}>
          {translation('personalArea.workspaceSettings.leaveWorkspace')}
        </RedActionButton>
      ) : null}
      {isWorkspaceOwner ? (
        <RedActionButton onClick={onClickDeleteWorkspace}>
          {translation('personalArea.workspaceSettings.deleteWorkspace')}
        </RedActionButton>
      ) : null}
      <DeleteWorkspaceModal
        isVisible={isDeleteWorkspaceModalVisible}
        setIsVisible={setIsDeleteWorkspaceModalVisible}
        onDeleteConfirm={onWorkspaceDeleteConfirm}
      />
      <LeaveWorkspaceModal
        workspaceName={workspaceName}
        isVisible={isLeaveWorkspaceModalVisible}
        setIsVisible={setIsLeaveWorkspaceModalVisible}
        onLeave={onLeaveWorkspace}
      />
    </>
  );
};

export default WorkspaceActions;
