import React, { useContext, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { workspaceContext } from '../../../../state';
import InviteMembersModal from '../../../modalsComponents/components/invite-members';
import { renameWorkspace } from '../../api';
import { InviteMembersButton, SmallGreyText, SubtitleWorkspace, WrapperHeaderNameWorkspace } from './styles';
import { WorkspaceName } from './workspace-name';

const MembersPageTitle: React.FC = () => {
  const [inviteModalVisible, setInviteModalVisible] = useState<boolean>(false);

  const workspace = useContext(workspaceContext);

  const { t: translation } = useTranslation();

  const onOpenInvite = (): void => {
    setInviteModalVisible(true);
  };

  const onWorkspaceNameChange = (newName: string): void => {
    newName = (newName || '').trim().replace(/\s+/ig, ' ');
    if (newName === workspace.name) {
      return;
    }

    workspace.updateWorkspace({
      name: newName,
    });

    workspace.updateAvailableWorkspaces(workspace.availableWorkspaces.map(
      availableWorkspace => availableWorkspace.id === workspace.id ?
        { ...availableWorkspace, name: newName } :
        availableWorkspace,
    ));

    renameWorkspace(workspace.id, newName);
  };

  // desired behavior to replace 0 members to 1
  let maxMembersText = '/' + (workspace.planMembersMax || 1).toString();
  if (workspace.isPlanUnlimited) {
    maxMembersText = '';
  }

  return (
    <WrapperHeaderNameWorkspace hasEditable={true}>
      <WorkspaceName
        canChangeName={workspace.permissions.editWorkspace}
        workspaceName={workspace.name}
        onNameChange={onWorkspaceNameChange}
      />
      {workspace.permissions.inviteMembers ? (
        <SubtitleWorkspace>
          <SmallGreyText>
            {workspace.members.length}
            {maxMembersText}
            {' '}
            {translation('personalArea.workspaceSettings.usersJoined')}
          </SmallGreyText>

          <SmallGreyText>
            {translation('personalArea.workspaceSettings.manageMembers')}
            {' '}
            <InviteMembersButton onClick={onOpenInvite}>
              {translation('personalArea.workspaceSettings.inviteMembers')}
            </InviteMembersButton>
          </SmallGreyText>
        </SubtitleWorkspace>
      ) : null}
      <InviteMembersModal
        visible={inviteModalVisible}
        onClose={(): void => setInviteModalVisible(false)}
      />
    </WrapperHeaderNameWorkspace>
  );
};

export default MembersPageTitle;
