import { TableCell } from '@material-ui/core';
import React, { FC, useEffect } from 'react';

import CellEmail from './email';
import CellInvited from './invited';
import CellPermissions from './permissions';
import {
  LastActivity,
} from './row-styles';
import { IWorkspaceMember } from '../../../interfaces/workspaces';
import { CustomTableRow } from '../../../ui/styles';
import dateToText from '../../../utils/date-to-text';

const MemberRow: FC<IWorkspaceMember> = (props) => {
  const { email, lastActiveAt, createdAt, invitedByEmail } = props;

  useEffect(() => {
    const preventScrolling = (e: any): void => e.preventDefault();

    window.addEventListener('scroll', preventScrolling);

    return () => window.removeEventListener('scroll', preventScrolling);
  }, []);

  return (
    <CustomTableRow>
      <TableCell style={{ paddingLeft: 0, width: '220px' }}>
        <CellEmail email={email} />
      </TableCell>
      <CellPermissions {...props} />
      <TableCell style={{ width: '160px' }}>
        <LastActivity>
          {dateToText(lastActiveAt)}
        </LastActivity>
      </TableCell>
      <TableCell
        style={{ width: '220px', overflow: 'hidden', textOverflow: 'ellipsis' }}
      >
        <CellInvited
          inviteCreatedAt={createdAt}
          invitedByEmail={invitedByEmail}
        />
      </TableCell>
    </CustomTableRow>
  );
};

export default React.memo(MemberRow);
