import React, { useContext, useEffect, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';

import MembersTable from './members-table';
import MembersPageTitle from './title';
import WorkspaceActions from './workspaces-actions';
import { IMySharedAccount } from '../../../interfaces';
import { IWorkspaceMember } from '../../../interfaces/workspaces';
import { workspaceContext } from '../../../state';
import { SmallGreyText } from '../../../ui/styles';
import { requestWorkspace, sendActionAnalytics } from '../../common/api';
import LoadingPage from '../../common/loading-page';
import { requestGetMySharedAccounts } from '../../modalsComponents/components/share-account-modal/api';
import { TabWrapper } from '../../personalArea/menu/tab-wrapper';
import { removeProfileInvite } from '../../quickProfiles/api';

interface ILimitedAndGlobalMembers {
  limitedMembers: IWorkspaceMember[];
  globalMembers: IWorkspaceMember[];
}

const MembersPage: React.FC = () => {
  const [mySharedAccounts, setMySharedAccounts] = useState<IMySharedAccount[]>([]);

  const workspace = useContext(workspaceContext);

  const { t: translation } = useTranslation();

  useEffect(() => {
    sendActionAnalytics('visited members page');

    const updateWorkspace = async (): Promise<void> => {
      const workspaceData = await requestWorkspace(workspace.id);
      workspace.updateWorkspace({ ...workspaceData });
    };

    const requestAccountShares = async (): Promise<void> => {
      const sharedAccounts = await requestGetMySharedAccounts();
      setMySharedAccounts(sharedAccounts);
    };

    requestAccountShares();
    updateWorkspace();
  }, []);

  const { limitedMembers, globalMembers } = useMemo(() => workspace.members
    .reduce<ILimitedAndGlobalMembers>((result, member) => {
      if (member.limitedAccess) {
        result.limitedMembers.push(member);
      } else {
        result.globalMembers.push(member);
      }

      return result;
    }, { limitedMembers: [], globalMembers: [] }), [workspace.members]);

  const hideLimitedMembers = !limitedMembers.length || (workspace.members.length === 1 && !workspace.permissions.inviteMembers);
  const shouldShowNoMembersText = workspace.me?.limitedAccess && hideLimitedMembers;

  const onRemoveMySharedAccount = async (shareAccountId: string): Promise<void> => {
    await removeProfileInvite(shareAccountId);
    setMySharedAccounts([...mySharedAccounts.filter(accountShared => accountShared.id !== shareAccountId)]);
  };

  if (!workspace.isLoaded) {
    return <LoadingPage />;
  }

  return (
    <TabWrapper tabKey='members' HelmetTitle={`Members - GoLogin ${window.gologinAppVersion || ''}`}>
      <MembersPageTitle />
      {globalMembers.length && !workspace.me?.limitedAccess ? (
        <MembersTable
          title={translation('personalArea.workspaceSettings.accessToWorkspace')}
          members={globalMembers}
          mySharedAccounts={mySharedAccounts}
          onRemoveMySharedAccount={onRemoveMySharedAccount}
        />
      ) : null}
      {!hideLimitedMembers ? (
        <MembersTable
          title={translation('personalArea.workspaceSettings.accessToFolders')}
          members={limitedMembers}
        />
      ) : null}
      {shouldShowNoMembersText ? (
        <SmallGreyText>
          {translation('personalArea.workspaceSettings.limitedAccessNotice')}
        </SmallGreyText>
      ) : null}
      <WorkspaceActions />
    </TabWrapper>
  );
};

export default MembersPage;
