import styled from '@emotion/styled';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { IWorkspaceFolderRole } from '../../../../interfaces/workspaces';

export const GreyText = styled('span')`
  color: var(--00000080-members-row);
  letter-spacing: 0.4px;
  font-size: 12px;
  line-height: 16px;
`;

interface IFoldersGreyText {
  limitedAccess: boolean;
  folders: IWorkspaceFolderRole[];
}

const FoldersGreyText: React.FC<IFoldersGreyText> = (props) => {
  const { limitedAccess, folders } = props;

  const { t: translation } = useTranslation();

  if (!(limitedAccess && folders.length)) {
    return null;
  }

  let foldersText = translation('personalArea.workspaceSetting.inFolder');
  if (folders.length > 1) {
    foldersText = translation('personalArea.workspaceSetting.inFolders', { foldersCount: folders.length });
  }

  return (
    <div style={{ marginTop: -2 }}>
      <GreyText>
        {foldersText}
      </GreyText>
    </div>
  );
};

export default FoldersGreyText;
