import { message } from 'antd';
import React from 'react';
import { TFunction } from 'i18next';

import { IWorkspaceFolderRole, WorkspaceRole } from '../../../../interfaces/workspaces';
import { IWorkspaceCtx } from '../../../../state';
import { kickMember, updateMemberPermissions } from '../../api';

interface IMemberProto {
  limitedAccess: boolean;
  role: WorkspaceRole;
  folders: IWorkspaceFolderRole[];
}

export const editMemberPermissions = (
  workspace: IWorkspaceCtx,
  memberId: string,
  newState: IMemberProto,
): Promise<void> => {
  const { limitedAccess, role, folders } = newState;

  workspace.updateWorkspace({
    members: workspace.members.map(member => member.id === memberId ? {
      ...member,
      role,
      limitedAccess,
      folders,
    } : member),
  });

  return updateMemberPermissions({
    workspaceId: workspace.id,
    memberId,
    limitedAccess,
    role,
    folders,
  });
};

export const deleteMember = (workspace: IWorkspaceCtx, memberId: string, translation: TFunction): Promise<void> => {
  const kickedMemberEmail = workspace.members.find(member => member.id === memberId)?.email || '';

  workspace.updateWorkspace({
    members: workspace.members.filter(member => member.id !== memberId),
  });

  message.success(
    <div style={{ display: 'inline-block' }}>
      <span style={{ fontWeight: 700, fontFamily: 'Roboto' }}>
        {kickedMemberEmail}
      </span>
      {' '}
      {translation('permissionSelectMenu.removeMember.removed')}
    </div>,
  );

  return kickMember(workspace.id, memberId);
};
