import { IWorkspaceFolderRole, WorkspaceRole } from '../../../interfaces/workspaces';

type ROLE = 'owner' | 'admin' | 'editor' | 'guest' | 'varies' | 'noAccess';

const rawFromLimitedAccess = (folders: IWorkspaceFolderRole[]): ROLE =>
  folders.reduce<ROLE>((role, folder) => {
    if (!role || role === folder.role) {
      return folder.role === 'owner' ? 'admin' : folder.role;
    }

    return 'varies';
  }, folders.length ? folders[0].role : 'noAccess');

const getPermissionsRoleTitle = (
  workspaceRole: WorkspaceRole,
  limitedAccess: boolean,
  folders: IWorkspaceFolderRole[],
): ROLE => {
  if (!limitedAccess) {
    return workspaceRole;
  }

  return rawFromLimitedAccess(folders);
};

export default getPermissionsRoleTitle;
