import { Table, TableBody, TableHead, TableRow } from '@material-ui/core';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { TableTitle } from './page-styles';
import { IMySharedAccount } from '../../../interfaces';
import { IWorkspaceMember } from '../../../interfaces/workspaces';
import { TableHeaderCell } from '../../../ui/styles';
import MemberRow from '../member-row';
import MySharedAccountRow from '../my-shared-account-row';

interface IMembersTable {
  title: string;
  members: IWorkspaceMember[];
  mySharedAccounts?: IMySharedAccount[];
  onRemoveMySharedAccount?: (id: string) => Promise<void>;
}

const MembersTable: React.FC<IMembersTable> = (props) => {
  const { title, members, mySharedAccounts = [], onRemoveMySharedAccount } = props;

  const { t: translation } = useTranslation();

  const removeMySharedAccount = (id: string): void => {
    if (!onRemoveMySharedAccount) {
      return;
    }

    onRemoveMySharedAccount(id);
  };

  return (
    <div style={{ margin: 0, width: '800px' }}>
      <TableTitle>
        {title}
      </TableTitle>
      <Table style={{ width: '100%', marginBottom: 28, marginTop: 16 }}>
        <TableHead>
          <TableRow>
            <TableHeaderCell style={{ width: '220px', paddingLeft: 0 }}>
              {translation('personalArea.workspaceSettings.membersTable.member')}
            </TableHeaderCell>
            <TableHeaderCell style={{ width: '200px' }}>
              {translation('personalArea.workspaceSettings.membersTable.permission')}
            </TableHeaderCell>
            <TableHeaderCell style={{ width: '160px' }}>
              {translation('personalArea.workspaceSettings.membersTable.lastActive')}
            </TableHeaderCell>
            <TableHeaderCell style={{ width: '220px' }}>
              {translation('personalArea.workspaceSettings.membersTable.invitedBy')}
            </TableHeaderCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {members.map(member => (
            <MemberRow key={member.id} {...member} />
          ))}
          {mySharedAccounts.map(mySharedAccount => (
            <MySharedAccountRow
              key={mySharedAccount.id}
              mySharedAccount={mySharedAccount}
              onRemoveMySharedAccount={async (): Promise<void> => removeMySharedAccount(mySharedAccount.id)}
            />
          ))}
        </TableBody>
      </Table>
    </div>
  );
};

export default MembersTable;
