import React, { useContext, useEffect, useLayoutEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { IForMemberPermissions, IWorkspaceFolderRole, IWorkspaceMember, WorkspaceRole } from '../../../../interfaces/workspaces';
import { workspaceContext } from '../../../../state';
import { getInviteActionText } from '../../../../utils/roles-texts';
import PermissionsSelect from '../../permissions-select';
import getPermissionsRoleTitle from '../../role-select/row-role-title';
import { SelectButton } from '../../role-select/select-button';
import { FoldersPermissionsPopover, PermissionsTableCell } from '../row-styles';
import showDeleteConfirm from './delete-member-confirm';
import FoldersGreyText from './folders-grey-text';
import { deleteMember, editMemberPermissions } from './member-actions';

interface ICellPermissions extends Partial<IWorkspaceMember> {
  id: string;
  email: string;
  role: WorkspaceRole;
  limitedAccess: boolean;
  folders?: IWorkspaceFolderRole[];
  permissions: IForMemberPermissions;
}

const CellPermissions: React.FC<ICellPermissions> = (props) => {
  const { id: memberId, email, role, limitedAccess, folders = [], permissions } = props;

  const [currentLimitedAccess, setCurrentLimitedAccess] = useState<boolean>(limitedAccess);
  const [currentRole, setCurrentRole] = useState<WorkspaceRole>(role);
  const [currentFolders, setCurrentFolders] = useState<IWorkspaceFolderRole[]>(limitedAccess ? folders : []);
  const [anchorEl, setAnchorEl] = useState<HTMLElement|null>(null);
  const [permissionPopoverHeight, setPermissionPopoverHeight] = useState<number>(0);
  const [popoverPositionVertical, setPopoverPositionVertical] = useState<number>(0);

  const workspace = useContext(workspaceContext);

  const { t: translation } = useTranslation();

  useEffect(() => {
    onPermissionsChange(limitedAccess, role, limitedAccess ? folders : []);
  }, [limitedAccess, role, folders]);

  useLayoutEffect(() => {
    if (!(anchorEl && permissionPopoverHeight)) {
      return;
    }

    const availableHeight = document.body.clientHeight - anchorEl.getBoundingClientRect().top;
    const offset = anchorEl.clientHeight / 2;
    let heightToSet = permissionPopoverHeight + offset;
    const minimalHeightDiff = 20;
    if (availableHeight - permissionPopoverHeight > minimalHeightDiff) {
      heightToSet = -offset;
    }

    setPopoverPositionVertical(heightToSet);
  }, [anchorEl, currentLimitedAccess, permissionPopoverHeight]);

  const fixFoldersPopover = (element: Element): void => {
    setPermissionPopoverHeight(element.clientHeight);
  };

  const onClose = (): void => {
    setAnchorEl(null);
    const [bodyChildElement] = document.getElementsByTagName('body');
    if (bodyChildElement.hasAttribute('style')) {
      bodyChildElement.removeAttribute('style');
    }

    const hasLimitedAccessChanged = limitedAccess !== currentLimitedAccess;
    const hasRoleChanged = role !== currentRole;
    const hasFoldersSizeChanged = currentFolders.length !== folders.length;
    const hasFolderRoleChanged = currentFolders.some(currentFolder => {
      const oldFolder = folders.find(folder => currentFolder.name === folder.name);

      return currentFolder.role !== oldFolder?.role;
    });

    const haveFoldersChanged = currentLimitedAccess && (hasFoldersSizeChanged || hasFolderRoleChanged);
    const hasPermissionUpdates = hasLimitedAccessChanged || hasRoleChanged || haveFoldersChanged;
    if (hasPermissionUpdates) {
      editMemberPermissions(workspace, memberId, {
        limitedAccess: currentLimitedAccess,
        role: currentRole,
        folders: currentFolders,
      });
    }
  };

  const isMyRow = memberId === workspace.me?.id;
  const canManageInFolders = currentLimitedAccess && workspace.folders.some(folder => folder.permissions.manageMember);
  const canManagePermissions = !isMyRow && (permissions.manageWorkspaceWideAccess || canManageInFolders);

  const onDeletePressed = (): void => {
    onClose();
    showDeleteConfirm(email, () => deleteMember(workspace, memberId, translation), translation);
  };

  const onPermissionsChange = (
    newLimitedAccess: boolean,
    newRole: WorkspaceRole,
    newFolders: IWorkspaceFolderRole[],
  ): void => {
    setCurrentLimitedAccess(newLimitedAccess);
    setCurrentRole(newRole);
    setCurrentFolders(newFolders);
  };

  const onPermissionsOpen = (e: React.MouseEvent<HTMLElement>): void => {
    if (!anchorEl && canManagePermissions) {
      setAnchorEl(e.currentTarget);
    }
  };

  const currentRoleTitle = getPermissionsRoleTitle(currentRole, currentLimitedAccess, currentFolders);

  return (
    <PermissionsTableCell
      limitedAccess={!!(currentLimitedAccess && currentFolders.length)}
      hoverable={canManagePermissions}
      onClick={onPermissionsOpen}
    >
      <SelectButton
        roleText={getInviteActionText(currentRoleTitle, translation)}
        disabled={!canManagePermissions}
        width='264px'
        height='20px'
      />
      <FoldersGreyText limitedAccess={currentLimitedAccess} folders={currentFolders} />
      <FoldersPermissionsPopover
        open={!!anchorEl}
        anchorEl={anchorEl}
        onClose={onClose}
        getContentAnchorEl={fixFoldersPopover as (e: Element) => Element}
        transformOrigin={{ vertical: popoverPositionVertical, horizontal: 24 }}
      >
        <PermissionsSelect
          limitedAccess={currentLimitedAccess}
          role={currentRole === 'owner' ? 'admin' : currentRole}
          folders={currentFolders ?? []}
          onChange={onPermissionsChange}
          isCurrentMemberGlobalAdmin={permissions.manageWorkspaceWideAccess}
          showRemoveMember={permissions.kickMember}
          onRemoveMember={onDeletePressed}
          blockWorkspaceWideAdminSelect={!permissions.manageWorkspaceWideAdminAccess}
        />
      </FoldersPermissionsPopover>
    </PermissionsTableCell>
  );
};

export default CellPermissions;
